.App {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.box {
  width: 100%;
  height: 100px;
  background-color: red;
}
.search {
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: center;
  margin: 100px;
}

.search input {
  width: 300px;
  height: 50px;
  border-radius: 10px;
  font-size: 30px;
  background-color: transparent;
  font-family: inherit;
  padding: 10px;
  border: none;
}
.search input:focus {
  outline: 0;
}

.movie {
  width: 250px;
  height: 510px;
  border-radius: 10px;
  margin: 2.5rem;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  transition: all 0.3s linear;
  display: flex;
  flex-direction: column;
  text-align: center;
  text-decoration: none;

}
.movie:hover {
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.7);
  margin: 2rem 2.5rem 3rem;
}

.movie-header img {
  max-width: 100%;
  height: 25rem;
  opacity: 0.9;
  filter: drop-shadow(2px 2px 5px black);
  display: block;
  margin-left: auto;
  margin-right: auto;
  object-fit: cover;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
  cursor: pointer;
}

.movie-info h3 {
  text-decoration: none;
  cursor: pointer;
  font-size: 23px;
  border-radius: 10px;
}

.lds-dual-ring {
  display: inline-block;
  width: 80px;
  height: 80px;
}
.lds-dual-ring:after {
  content: " ";
  display: block;
  width: 64px;
  height: 64px;
  margin: 8px;
  border-radius: 50%;
  border: 6px solid red;
  border-color: red transparent red transparent;
  animation: lds-dual-ring 1.2s linear infinite;
}
@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}


.single-movie{
  display: flex;
  justify-content: center;
 
}

.single-info{
  display: flex;
  flex-direction: column;
  margin-left: 2.5rem;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
  Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.single-info h2 {
  max-width: 35em;
  font-size: 3rem;
  margin-top: 1.5rem;

}

.single-info h3 {
  max-width: 20rem;
  font-size: 1.2rem;
  margin-top: 0.5rem;

}


.single-info p {
  max-width: 35em;
  font-size: 1.2rem;
  margin-top: 0.5rem;
  line-height: 1.5;

}

.btn {
  width:150px;
  text-transform: uppercase;
  text-align: center;
  padding: 0.25rem 0.5rem;
  background: hsl(182, 74%, 31%);
  color: white;
  border-top-right-radius: 20px;
  border-bottom-left-radius: 20px;
  display: block;
  margin-top: 0.5rem;
  letter-spacing: 1px;
  text-decoration: none;
}


.loading{
  font-size: 30px;
 
}